import React from 'react';

const BackgroundSpirals: React.FC = () => {
    return (
        <>
            <style>{`
                @keyframes float {
                    0%, 100% { transform: translateY(0px); }
                    50% { transform: translateY(-20px); }
                }
                
                @keyframes glow {
                    0%, 100% { filter: drop-shadow(0 0 8px rgba(255,45,45,0.4)); }
                    50% { filter: drop-shadow(0 0 16px rgba(255,45,45,0.8)); }
                }
                
                .spiral-container {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    z-index: 0;
                    pointer-events: none;
                    overflow: hidden;
                }
                
                .spiral-svg {
                    width: 100%;
                    height: 100%;
                }
                
                .network-node {
                    animation: glow 4s ease-in-out infinite;
                }
                
                .network-node-1 { animation-delay: 0s; }
                .network-node-2 { animation-delay: 0.5s; }
                .network-node-3 { animation-delay: 1s; }
                .network-node-4 { animation-delay: 1.5s; }
                .network-node-5 { animation-delay: 2s; }
                .network-node-6 { animation-delay: 2.5s; }
                .network-node-7 { animation-delay: 3s; }
                .network-node-8 { animation-delay: 3.5s; }
            `}</style>
            
            <div className="spiral-container">
                <svg 
                    className="spiral-svg" 
                    viewBox="0 0 1400 900" 
                    preserveAspectRatio="xMidYMid slice"
                    xmlns="http://www.w3.org/2000/svg"
                >
                    <defs>
                        <linearGradient id="networkGradient" x1="0%" y1="0%" x2="100%" y2="100%">
                            <stop offset="0%" stopColor="rgba(255,45,45,0.3)" />
                            <stop offset="50%" stopColor="rgba(255,45,45,0.15)" />
                            <stop offset="100%" stopColor="rgba(255,45,45,0.05)" />
                        </linearGradient>
                    </defs>
                    
                    {/* Connection lines between nodes */}
                    <line x1="200" y1="150" x2="600" y2="300" stroke="rgba(255,45,45,0.2)" strokeWidth="1.5" />
                    <line x1="200" y1="150" x2="400" y2="600" stroke="rgba(255,45,45,0.15)" strokeWidth="1" />
                    <line x1="600" y1="300" x2="700" y2="450" stroke="rgba(255,45,45,0.2)" strokeWidth="1.5" />
                    <line x1="600" y1="300" x2="900" y2="200" stroke="rgba(255,45,45,0.15)" strokeWidth="1" />
                    <line x1="700" y1="450" x2="1100" y2="300" stroke="rgba(255,45,45,0.2)" strokeWidth="1.5" />
                    <line x1="700" y1="450" x2="1000" y2="650" stroke="rgba(255,45,45,0.15)" strokeWidth="1" />
                    <line x1="1100" y1="300" x2="1200" y2="600" stroke="rgba(255,45,45,0.15)" strokeWidth="1" />
                    <line x1="400" y1="600" x2="1000" y2="650" stroke="rgba(255,45,45,0.15)" strokeWidth="1" />
                    <line x1="900" y1="200" x2="1100" y2="300" stroke="rgba(255,45,45,0.1)" strokeWidth="1" />
                    
                    {/* Central connections */}
                    <line x1="700" y1="450" x2="200" y2="150" stroke="rgba(255,45,45,0.12)" strokeWidth="1" />
                    <line x1="700" y1="450" x2="1200" y2="600" stroke="rgba(255,45,45,0.12)" strokeWidth="1" />
                    <line x1="700" y1="450" x2="400" y2="600" stroke="rgba(255,45,45,0.12)" strokeWidth="1" />
                    
                    {/* Network nodes - Top Left */}
                    <g className="network-node network-node-1">
                        <circle cx="200" cy="150" r="8" fill="rgba(255,45,45,0.8)" opacity="0.9" />
                        <circle cx="200" cy="150" r="12" fill="none" stroke="rgba(255,45,45,0.4)" strokeWidth="1.5" />
                    </g>
                    
                    {/* Network nodes - Top Center */}
                    <g className="network-node network-node-2">
                        <circle cx="600" cy="300" r="7" fill="rgba(255,45,45,0.7)" opacity="0.85" />
                        <circle cx="600" cy="300" r="11" fill="none" stroke="rgba(255,45,45,0.35)" strokeWidth="1.5" />
                    </g>
                    
                    {/* Network nodes - Top Right */}
                    <g className="network-node network-node-3">
                        <circle cx="900" cy="200" r="6" fill="rgba(255,45,45,0.6)" opacity="0.8" />
                        <circle cx="900" cy="200" r="10" fill="none" stroke="rgba(255,45,45,0.3)" strokeWidth="1" />
                    </g>
                    
                    {/* Network nodes - Right */}
                    <g className="network-node network-node-4">
                        <circle cx="1100" cy="300" r="8" fill="rgba(255,45,45,0.75)" opacity="0.9" />
                        <circle cx="1100" cy="300" r="12" fill="none" stroke="rgba(255,45,45,0.4)" strokeWidth="1.5" />
                    </g>
                    
                    {/* Network nodes - Center */}
                    <g className="network-node network-node-5">
                        <circle cx="700" cy="450" r="9" fill="rgba(255,45,45,0.85)" opacity="0.95" />
                        <circle cx="700" cy="450" r="14" fill="none" stroke="rgba(255,45,45,0.45)" strokeWidth="2" />
                    </g>
                    
                    {/* Network nodes - Bottom Right */}
                    <g className="network-node network-node-6">
                        <circle cx="1000" cy="650" r="7" fill="rgba(255,45,45,0.7)" opacity="0.85" />
                        <circle cx="1000" cy="650" r="11" fill="none" stroke="rgba(255,45,45,0.35)" strokeWidth="1.5" />
                    </g>
                    
                    {/* Network nodes - Bottom Left */}
                    <g className="network-node network-node-7">
                        <circle cx="400" cy="600" r="7" fill="rgba(255,45,45,0.65)" opacity="0.8" />
                        <circle cx="400" cy="600" r="11" fill="none" stroke="rgba(255,45,45,0.32)" strokeWidth="1.5" />
                    </g>
                    
                    {/* Network nodes - Far Right */}
                    <g className="network-node network-node-8">
                        <circle cx="1200" cy="600" r="6" fill="rgba(255,45,45,0.55)" opacity="0.75" />
                        <circle cx="1200" cy="600" r="10" fill="none" stroke="rgba(255,45,45,0.28)" strokeWidth="1" />
                    </g>
                    
                    {/* Additional small nodes for complexity */}
                    <g className="network-node network-node-3">
                        <circle cx="300" cy="400" r="5" fill="rgba(255,45,45,0.5)" opacity="0.7" />
                        <circle cx="300" cy="400" r="8" fill="none" stroke="rgba(255,45,45,0.25)" strokeWidth="1" />
                    </g>
                    
                    <g className="network-node network-node-6">
                        <circle cx="1050" cy="500" r="5" fill="rgba(255,45,45,0.5)" opacity="0.7" />
                        <circle cx="1050" cy="500" r="8" fill="none" stroke="rgba(255,45,45,0.25)" strokeWidth="1" />
                    </g>
                </svg>
            </div>
        </>
    );
};

export default BackgroundSpirals;
